'use client'

import {
  Line,
  LineChart,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
  CartesianGrid,
} from 'recharts'
import type { LifeMonthlyPoint } from '@/modules/core/domain/life-types'

interface LifeTimelineChartProps {
  data: LifeMonthlyPoint[]
}

export function LifeTimelineChart({ data }: LifeTimelineChartProps) {
  const yearlyPoints = data.filter(point => point.date.getMonth() === 0)

  const chartData = yearlyPoints.map(point => ({
    year: point.date.getFullYear(),
    netWorth: Math.round(point.netWorth),
    realNetWorth: Math.round(point.realNetWorth),
  }))

  if (chartData.length === 0) {
    return (
      <div className="flex h-64 items-center justify-center rounded-2xl border border-slate-800 bg-slate-900/40 text-sm text-slate-500">
        Configure seus dados iniciais para ver a projeção patrimonial.
      </div>
    )
  }

  return (
    <div className="h-72 w-full rounded-2xl border border-slate-800 bg-slate-900/40 p-4">
      <ResponsiveContainer width="100%" height="100%">
        <LineChart data={chartData}>
          <CartesianGrid strokeDasharray="3 3" stroke="#1f2937" />
          <XAxis
            dataKey="year"
            stroke="#6b7280"
            tickLine={false}
          />
          <YAxis
            stroke="#6b7280"
            tickLine={false}
          />
          <Tooltip
            contentStyle={{
              backgroundColor: '#020617',
              borderRadius: 8,
              border: '1px solid #1f2937',
              fontSize: 12,
            }}
          />
          <Line
            type="monotone"
            dataKey="netWorth"
            stroke="#3b82f6"
            strokeWidth={2}
            dot={false}
            name="Patrimônio (nominal)"
          />
          <Line
            type="monotone"
            dataKey="realNetWorth"
            stroke="#22c55e"
            strokeWidth={2}
            dot={false}
            name="Patrimônio (real)"
          />
        </LineChart>
      </ResponsiveContainer>
    </div>
  )
}

